import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import style from "../InstituteFeedback.module.css";
import { Suspense, lazy, useState } from "react";
import { useLocation } from "react-router-dom";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
const OneStaffAnalytic = lazy(() =>
  import("./OneStaffAnalytic/OneStaffAnalytic")
);

const StudentFeedbackAllStaffCard = ({ staff }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [open, setOpen] = useState(false);

  const onOpen = () => {
    if (staff?.student_feedback?.[0]?.analytic_evaluation)
      setOpen((pre) => !pre);
  };
  const onToggle = () => {
    setOpen((pre) => !pre);
  };

  // console.info("getQuery", getQuery);
  return (
    <>
      <div className={style.sf_card} onClick={onOpen}>
        <div
          className={style.sf_card_contatiner}
          style={{
            justifyContent: "space-between",
            width: "100%",
          }}
        >
          <div className={style.sf_card_contatiner}>
            <img
              src={
                staff?.staffProfilePhoto
                  ? `${imageShowUrl}/${staff?.staffProfilePhoto}`
                  : `${assestsFeedUrl}/feed-user-avatar.svg`
              }
              loading="lazy"
              alt="avatar"
            />

            <div className={style.sf_card_contatiner_inner}>
              <h6>
                {`${staff?.staffFirstName ?? ""} ${
                  staff?.staffMiddleName ?? ""
                } ${staff?.staffLastName ?? ""}`}
              </h6>
              <p>
                {t("employee_code")}
                {": "}
                {staff?.staffROLLNO ?? ""}
                {/* {staff?.staff_emp_code ?? ""} */}
              </p>
            </div>
          </div>
          {staff?.student_feedback?.[0]?.analytic_evaluation ? (
            <div className={style.sf_card_contatiner_inner}>
              <h6>
                <span
                  style={{
                    fontWeight: "400",
                  }}
                >
                  {t("total_feedback")}
                </span>
                {": "}
                {getInPercentage(
                  staff?.student_feedback?.[0]?.subject_analytic
                )}
                %
              </h6>
              <p>
                {t("given_feedback")}
                {": "}
                {staff?.student_feedback?.[0]?.student_give_feedback_count ?? 0}
              </p>
            </div>
          ) : null}
        </div>
      </div>
      <Suspense fallback={<QvipleLoader />}>
        {open && (
          <OneStaffAnalytic
            onClose={onToggle}
            ifid={getQuery.state?.ifid}
            sid={staff?._id}
            staff={staff}
          />
        )}
      </Suspense>
    </>
  );
};

export default StudentFeedbackAllStaffCard;

const getInPercentage = (list) => {
  if (list?.length > 0) {
    let sum = 0;
    for (let lt of list) {
      sum += lt?.feedback_percentage;
    }
    sum = Math.floor(sum / list?.length);
    return sum;
  }
  return 0;
};
